import React from 'react'

import bajuBodo from '../../assets/images/baju-bodo-illustration.png'
import thanksImg from '../../assets/images/thanks-2.png'

const Penutup = () => {
    return (
        <div className="penutup-container">
            <div className='thanks-img'>
                <img src={thanksImg} />
            </div>
            <h1 className="title" data-aos="fade-up">Terimakasih</h1>
            <div className="info" data-aos="fade-up">
                <p className="deskripsi" >Berdasarkan kondisi saat ini, tanpa mengurangi rasa hormat, diharapkan tetap bisa menjalin silaturahmi melalui media online, bukan dengan kontak fisik secara langsung. Mohon pengertiannya.</p>
                <p className="salam">
                    Atas segala do'a dan restu saudara/i, <br />
                    kami ucapkan terima kasih. <br />
                    Wassalamualaikum warahmatullahi wabarakatuh.
                </p>

                <div className='illustration'>
                    <img src={bajuBodo} />
                </div>


            </div>
        </div>
    )
}

export default Penutup
