import axios from 'axios';
import React, { useState, useEffect } from 'react'
import { useParams } from 'react-router';
import getNameById from '../../assets/guests/guest';
import cincin from '../../assets/images/cincin.png'
import goDown from '../../assets/images/go_down.png'
import Navbar from '../../helper/components/Navbar'

const Header = ({ more, heightHead }) => {
    let { id } = useParams();

    const [fullName, setFullName] = useState("Tamu Undangan")
    const [showUndangan, setShowUndangan] = useState("hide")

    // function getFullName() {
    //     var config = {
    //         method: 'get',
    //         url: `https://sheet.best/api/sheets/1e3b25c2-d8d5-45a3-a43c-2e6f5176e27d/id/${id}`,
    //         headers: {}
    //     };

    //     axios(config)
    //         .then(function (response) {
    //             if ((response?.data?.length ?? 0) > 0) {
    //                 setFullName(response.data[0].name)
    //             }
    //             setShowUndangan("show")
    //         })
    //         .catch(function (error) {
    //             console.log(error)
    //             setShowUndangan("show")
    //         });
    // }

    function getFullName(){
        const guest = getNameById(id)
        if(guest){
            setFullName(guest.fullName)
        }
        setShowUndangan("show")
    }

    useEffect(() => {
        getFullName()
    }, [])

    return (
        <div className="header-container" id="home-wed" style={heightHead ? { minHeight: "100%" } : { minHeight: "100vh" }} >
            <Navbar />
            <div className="info" style={{ zIndex: 0 }} data-aos="fade-up" >
                <div className="info-undangan">
                    <img src={cincin} alt="" />
                    <h1>Manca &#38; Marni</h1>
                    <h4>25 Maret 2022</h4>
                </div>
                <div className={showUndangan}>
                    <div className="info-tamu">
                        <p>Yang terhormat:</p>
                        <p>{fullName} </p>
                        <div></div>
                    </div>
                    <div className="btn-more">
                        <a href="#undangan-wed" onClick={more}>
                            <img src={goDown} alt="" />
                        </a>
                        <p>Info Lebih Lanjut</p>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Header
